<template>
    <div class="setter">
        <navigate title="设置" position="absolute"/>
        <div class="scroll-wrapper" style="padding-top: 15px">
            <mu-list style="background-color: #fff; padding: 0">
                <mu-list-item class="list-item border-1px-b" to="/about/setter/brightness" button>
                    <mu-list-item-title>屏幕亮度</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item border-1px-b" to="/about/setter/mode" button>
                    <mu-list-item-title>模式切换</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item" to="/about/setter/notice" button>
                    <mu-list-item-title>消息通知</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
            <div style="height: 15px;"></div>
            <mu-list style="background-color: #fff; padding: 0">
                <mu-list-item class="list-item border-1px-b" to="/about/setter/update" button>
                    <mu-list-item-title>检查更新</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item" @click="_initAppDefault" button>
                    <mu-list-item-title>清除缓存<span class="cache-size">{{cacheSize}}M</span></mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
            <div style="height: 15px;"></div>
            <mu-list style="background-color: #fff; padding: 0">
                <mu-list-item class="list-item border-1px-b" to="/about/setter/related" button>
                    <mu-list-item-title>关于</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item" to="/about/setter/help" button>
                    <mu-list-item-title>功能介绍</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item border-1px-b" @click="guideClick" to="/controller" button>
                    <mu-list-item-title>新手指引</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </div>

        <!-- dialog -->
        <mu-dialog :open="cacheView" width="80%" dialog-class="dialog-style"
                   :esc-press-close="false" :overlay-close="false"
                   @close="dialogClose"
                   lock-scroll>
            <img style="display: block; width: 90px; height: 90px; margin: 0 auto 10px" :src="cacheIcon">
            <m-progress :progress="progress"/>
            <p style="padding-top: 20px; text-align: center">清除缓存中 ...</p>
        </mu-dialog>
        <!-- dialog -->

        <transition name="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue'
    import Navigate from "../../../base/navigate/navigate"
    import MProgress from "../../../base/progress/progress"
    import {mapActions} from 'vuex'
    import {
        appMixins,
        elementMixins,
        layerMixins,
        mapMixins,
        routeMixins,
        searchMixins,
        userMixins
    } from "../../../common/mixins"
    import {getSetters} from "../../../api/sys"
    import {getParams} from "../../../api/search"
    import {app} from "../../../common/js/cordova"
    import {
        changeTheme,
        getAppCache,
        clearHistory,
        changeTieldLayerVisible,
        changeChartLayerVisible,
        changeMapFramesServerState,
        showLayers,
        hideLayers
    } from '../../../common/js/cordova/map'
    import {setResources} from "../../../storage/app"


    export default {
        name: "setter",
        mixins: [
            appMixins,
            elementMixins,
            layerMixins,
            mapMixins,
            routeMixins,
            searchMixins,
            userMixins
        ],
        data() {
            return {
                cacheView: false,
                progress: 0,
                cacheIcon: 'static/image/Spin.gif',
                cacheSize: 0
            }
        },
        activated() {
            this.progress = 0

            this.getCacheSize()
        },
        methods: {
            _initAppDefault() {
                this.cacheView = true
                this.resetSpeech()
                this.resetText()
                this.resetBrightness()
                this.resetTheme()
                this.resetSpeechGender()
                this.resetLayers()
                this.resetMaps()
                this.resetRoutes()
                this.clearRouteHistory()
                this.clearRouteSearchHistory()
                this.clearSearchHistory()

                getParams('imgPrefixPath').then(res => {
                    setResources(res)

                    Vue.prototype.$images = `${res}`

                    console.log(`%c图片资源服务器地址： ${res}`, 'color: #66ccff')
                })


                const {offsetWidth, offsetHeight} = document.documentElement
                this.setScreen({width: offsetWidth, height: offsetHeight})


                document.documentElement.addEventListener('size', () => {
                    const {offsetWidth, offsetHeight} = document.documentElement
                    this.setScreen({width: offsetWidth, height: offsetHeight})
                })

                app.version().then(res => {
                    this.setVersion(res)
                })

                /***
                 * 初始化
                 */
                document.documentElement.addEventListener('touchstart', this.touchStart)
                document.documentElement.addEventListener('touchend', this.touchEnd)

                // 是否打开 GPS & 网络
                window.Arcgis && window.Arcgis.registerNetReceiver()
                window.Arcgis && window.Arcgis.observeWifiSwitch()


                // 路地图
                changeTieldLayerVisible(this.landMap)
                // 电子航道图
                changeChartLayerVisible(this.channelMap)

                // 图层点
                const layers = this.layers.map((item, index) => {
                    if (item.id === "013006") {
                        changeMapFramesServerState(item.status)
                    }
                    else if (item.status) {
                        showLayers([item.id])
                    }
                    else {
                        hideLayers([item.id])
                    }

                    return Object.assign({}, item, {
                        icon: this.LAYERS[index].icon,
                        id: this.LAYERS[index].id,
                        name: this.LAYERS[index].name,
                        color: this.LAYERS[index].color
                    })
                })

                this.setLayers(layers)

                // 主题
                changeTheme(this.theme)

                this.setModeTiming()

                // 搜索结果图标
                getSetters({key: 'SEARCH_RESULT_ICO'}).then(res => {
                    Array.isArray(res) && res.length > 0 && this.saveSearchIcons(res)
                })

                // 搜索类型图标
                getSetters({key: 'SEARCH_TYPE_ICO'}).then(res => {
                    Array.isArray(res) && res.length > 0 && this.setIcons(res)
                })

                // 搜索类型列表
                getSetters({key: 'SEARCH_TYPE_LIST'}).then(res => {
                    Array.isArray(res) && res.length > 0 && this.saveSearchList(res)
                })

                clearHistory()

                const timer = setInterval(time => {
                    this.progress += Number.parseInt(Math.random() * 500) / 100

                    if (this.progress >= 100) {
                        this.cacheView = false

                        this.progress = 0

                        this.getCacheSize()

                        clearInterval(timer)
                    }
                }, 100)
            },
            // 关闭弹出层
            dialogClose() {
                clearInterval(this.timer)
            },
            guideClick() {
                this.resetNovice()

                this.$router.push('/')
            },
            getCacheSize() {
                getAppCache().then(res => {
                    let size
                    try {
                        size = JSON.parse(res.toString()).cachesize
                    } catch (e) {
                        size = 0
                    }

                    this.cacheSize = (Math.round((size / 1024 / 1024) * 100) / 100).toFixed(0)
                })
            },
            ...mapActions({
                resetNovice: 'novice/resetNovice'
            }),
        },
        components: {
            MProgress,
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .setter
        padding-top 46px
        background-color #EFEFEF
        .scroll-wrapper
            width 100%
            height 100%
            .cache-size
                margin-left: 10px
            .border-1px-b:after
                left 16px
</style>



