<template>
    <div class="progress-wrapper">
        <mu-linear-progress style="border-radius: 100px"
                            mode="determinate"
                            :value="progress"
                            :max="max"
                            :size="size"/>
        <span class="progress-text">{{progressText}}</span>
    </div>
</template>

<script>
    export default {
        name: "m-progress",
        props: {
            progress: {
                type: Number,
                default: 0
            },
            size: {
                type: [Number, String],
                default: 20
            },
            max: {
                type: Number,
                default: 100
            }
        },
        computed: {
            progressText() {
                return `${Number.parseInt((this.progress / this.max) * 10000) / 100}%`
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .progress-wrapper
        position relative
        width 100%
        .progress-text
            position absolute
            top 50%
            left 50%
            font-size 14px
            color #fff
            transform translate3d(-50%, -50%, 0)
</style>



